import React, { useRef } from "react";
import useElementOnScreen from "../hooks/useElementOnScreen";
import { ContainerS } from "../components/Containers";
import apple from "../assets/images/Apple Store.svg";
import google from "../assets/images/Google play.svg";
import phones from "../assets/images/telephones.png";

const Banner = () => {

    const targetRef = useRef(null);
    const { isVisible, stayVisible } = useElementOnScreen("", targetRef)

    return (
        <>
            <ContainerS
                style={`flex-row gap-12 items-center text-left shadow-spurple rounded-2xl my-8 px-8
            ml:flex-col ml:text-center ml:py-4 ml:max-w-cxxs
            ${stayVisible && "appear"}
                fade-in
            `}>
                <div className={`w-full`} ref={targetRef}>
                    <h2 className="font-krub font-medium text-5xl ts:text-3xl ms:text-xl ts:mb-8">Disfruta de Selia en <br />
                        tu dispositivo movil</h2>
                    <div className="flex flex-row mt-4 ml:justify-center">
                        <img src={apple} alt="" width="135" className="mr-4" />
                        <img src={google} alt="" width="135" />
                    </div>
                </div>
                <div className="w-full  ml:hidden">
                    <img src={phones} alt="" />
                </div>
            </ContainerS>
        </>
    );
}

export default Banner;
