import React, { useState, useMemo, useEffect } from "react";

const useElementOnScreen = (options, targetRef) => {
    const [stayVisible, setStayVisible] = useState(false);
    const [isVisible, setIsVisible] = useState(false);

    const callbackOFunction = entries => {
        const [entry] = entries;
        setIsVisible(entry.isIntersecting)
        if (entry.isIntersecting) {
            setStayVisible(true);
        }
    }

    const appearOptions = useMemo(() => {
        if (options) {
            return options
        } else {
           return {
                root: null,
                rootMargin: "0px",
                threshold: 1
            }
        }
    }, [options]);

    useEffect(() => {
        const observer = new IntersectionObserver(callbackOFunction, appearOptions);
        const currentTarget = targetRef.current;
        if (currentTarget) {
            observer.observe(currentTarget)
        } 

        return () => {
            if (currentTarget) {
                observer.unobserve(currentTarget);
            }
        }
    }, [targetRef, appearOptions])
 
    return {isVisible, stayVisible};
}

export default useElementOnScreen;