import React, { useRef } from "react";
import useElementOnScreen from "../hooks/useElementOnScreen";
import { BtnPrimary } from "../components/Buttons"
import { H4 } from "../components/Headings";

const ServiceCard = ({ icon, alt, title, text, href, animation }) => {

    const targetRef = useRef(null);
    // When we need to take an html element from the dom, usin the ref can help us do it
    const {isVisible, stayVisible} = useElementOnScreen({
        root: null,
        rootMargin: "0px",
        threshold: 0.3
    }, targetRef)
    
    return (
        <div className={`flex flex-col m-2 4 w-full p-6 bg-purple-10 border-2 border-purple-50 rounded-2xl text-left fade-in 
        ${stayVisible && "appear"}
        ${animation}
        `}
            ref={targetRef}>
            <div className="flex flex-col ts:flex-row ts:items-center mb-4">
                <img src={icon} alt={alt} className="w-14 ts:w-10 mb-4 mr-4 " />
                <H4>{title}</H4>
            </div>
            <p className="body-text">{text}</p>
            <div className="w-full h-0.5 bg-purple-50 my-4"></div>
            <BtnPrimary href={href} style={"m-auto appear"}>Ver mas</BtnPrimary>

        </div>
    );
}

export default ServiceCard;
